	componentDidMount () {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme')
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light'
				document.body.attributes.setNamedItem(schemeAttribute)
			}
			if (type === 'VKWebAppGetUserInfoResult') {
				this.setState({fetchedUser: data})
			}
		})
		function fetchData() {
			bridge
			.send('VKWebAppGetUserInfo')
			.then(data => {
				this.setState({fetchedUser: data})
			})
			.catch(error => {
				console.log(error)
			})
		}
		fetchData()
		this.getGroups()
		this.getWeek()
	}
	getWeek(){
		let week = moment().week() % 2
		console.log(week)
		week ? this.setState({week, activeTab: 'lower week'}) : this.setState({week, activeTab: 'top week'})
		
	}

	async getGroups(){
		let response = await fetch('http://localhost:3000/groups/')
		let data = await response.json()
		console.log(data)
		this.setState({groups: data, popout: null})
	}
	// const go = e => {
